.offcanva {
  opacity: 0; /* opacity para trabajar con animaciones */
  height: 0;
  overflow: hidden;
  /* al parecer normalize tiene prioridad en estas propiedades por eso coloco el !important */
  transition-property: opacity !important;
  transition-duration: 500ms !important;
  transition: ease-in-out;
  /* mando a llamar esta clase desde offcanva.js */
  /* mando a llamar esta clase desde offcanva.js */
}
.offcanva__mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 2;
}
.offcanva__ocultar {
  opacity: 0;
}
.offcanva__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
.offcanva__cerrar-menu {
  display: flex;
  justify-content: flex-end;
  padding: 0.7rem 0;
}
.offcanva__enlace {
  font-weight: 700;
  font-size: 3.2rem;
  color: #FFFFFF;
}
.offcanva__enlace:hover {
  color: #cccccc;
}
.offcanva__enlace--actual {
  color: #784D3C;
  font-weight: 700;
}

