.modal {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  /* desde js mando a llamar la clase */
  /* desde js mando a llamar la clase */
}
.modal__form {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  border: 1rem;
  transform: translateY(-30%);
  transition-property: all !important;
  transition-duration: 1000ms !important;
  transition: ease-in-out;
  opacity: 0;
}
.modal__body {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.modal__gif {
  height: 20rem;
  transform: scale(1.2);
}
.modal__heading {
  margin: 2rem 0 0 0;
}
.modal__mensaje {
  display: none;
}
@media (min-width: 768px) {
  .modal__mensaje {
    display: block;
    text-align: center;
  }
}
.modal__boton {
  background-color: #784D3C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  text-transform: uppercase;
  display: inline-block;
  font-weight: 700;
  border: none;
}
@media (min-width: 768px) {
  .modal__boton {
    width: auto;
  }
}
.modal__boton:hover {
  cursor: pointer;
}
.modal__animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal__cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.galeria {
  /* contenedor general */
  /* contenedor individual */
}
.galeria__contenedor {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 300px);
  /* ESTA GALERIA SOLO SE VE BIEN PARA 10 IMAGENES */
  gap: 2rem;
}
@media (min-width: 1024px) {
  .galeria__contenedor {
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria__contenedor img {
  height: 300px;
  width: 359.55px;
  object-fit: cover;
  transition: transform 300ms ease;
}
.galeria__contenedor img:hover {
  transform: scale(1.1);
}
.galeria__li {
  position: relative;
  overflow: hidden;
}
.galeria__li:nth-child(4) {
  grid-column: 2/3;
  grid-row: 2/4;
}
.galeria__li:nth-child(8) {
  grid-column: 1/2;
  grid-row: 5/7;
}
.galeria__li:nth-child(4) img, .galeria__li:nth-child(8) img {
  height: 620px;
}
@media (min-width: 1024px) {
  .galeria__li {
    /* Reseteo los valores de la columna */
  }
  .galeria__li:nth-child(8) {
    grid-column: auto;
    grid-row: auto;
  }
  .galeria__li:nth-child(8) img {
    height: 300px;
  }
  .galeria__li:nth-child(4) {
    grid-column: 4/5;
    grid-row: 1/3;
  }
  .galeria__li:nth-child(6) {
    grid-column: 2/3;
    grid-row: 2/4;
  }
  .galeria__li:nth-child(4) img, .galeria__li:nth-child(6) img {
    height: 620px;
  }
}

